import './app-start.css';

export default class AppStart {
  constructor() {
    this.title = 'Youtube Client';
  }

  static render() {
    const searchIcon = document.createElement('link');
    searchIcon.rel = 'stylesheet';
    searchIcon.href = 'https://use.fontawesome.com/releases/v5.8.2/css/all.css';
    searchIcon.setAttribute('crossorigin', 'anonymous');
    searchIcon.integrity = 'sha384-oS3vJWv+0UjzBfQzYUhtDYW+Pj2yciDJxpsK1OYPAYjqT085Qq/1cq5FLXAZQ7Ay';

    document.head.appendChild(searchIcon);

    const viewport = document.createElement('meta');
    viewport.name = 'viewport';
    viewport.content = 'width=device-width';
    viewport.setAttribute('initial-scale', '1');

    document.head.appendChild(viewport);

    const content = document.createElement('div');
    content.setAttribute('class', 'container');
    content.innerHTML = '<div class="search"><i class="fas fa-search"></i><input class="input-search" type="search" '
      + 'maxlength="256" title="Search" placeholder="Search on YouTube"></div>';

    const videoContent = document.createElement('div');
    videoContent.setAttribute('class', 'video-content');

    const allClips = document.createElement('div');
    allClips.setAttribute('class', 'all-clips');
    videoContent.appendChild(allClips);

    content.appendChild(videoContent);

    document.body.appendChild(content);
  }
}
