export default class AppView {
  constructor() {
    this.videoItemWidth = 400;
    this.listeners = [];
    this.data = [];
  }

  prepareVideo = (video) => {
    const videoInfo = video.snippet;
    const { videoId } = video.id;
    const videoItem = document.createElement('div');
    videoItem.setAttribute('class', 'video-item');
    const date = new Date(videoInfo.publishedAt);

    videoItem.innerHTML = `<img alt="youtube video" class="image-preview" src="${videoInfo.thumbnails.medium.url}">
                            <div class="video-info"><h3 class="video-title">
                              <a href="https://www.youtube.com/watch?v=${videoId}" target="_blank">${videoInfo.title}</a>
                            </h3>
                            <span class="video-channel">${videoInfo.channelTitle}</span>
                            <span class="video-description"><em>${videoInfo.description}</em></span>
                            <div class="video-view_date">
                              <span class="video-view_count">Views: ${videoInfo.viewCount}</span>
                              <span class="video-date">${date.toLocaleDateString()}</span>
                            </div>
                            </div>`;
    return videoItem;
  };

  getArrowsParent = () => {
    const pageWrapper = document.createElement('div');
    pageWrapper.setAttribute('class', 'page-wrapper');
    return pageWrapper;
  };

  addStyleTransition(node) {
    // eslint-disable-next-line no-param-reassign
    node.style.transition = '1s ease';

    if (this.timeout) {
      clearTimeout(this.timeout);
    }
    // eslint-disable-next-line no-param-reassign
    this.timeout = setTimeout(() => { node.style.transition = ''; this.timeout = null; }, 1000);
  }

  updateStyleTransform(page) {
    [this.allClips] = document.getElementsByClassName('all-clips');
    this.mv = -this.videoItemWidth * this.countPerPage * (page - 1);
    this.allClips.style.transform = `translateX(${this.mv}px)`;
  }

  wrapLoadPage(targetPage, loadPage, currentPage) {
    return () => {
      this.currentPage = currentPage;
      this.targetPage = targetPage;
      const allClips = document.getElementsByClassName('all-clips')[0];
      if (this.currentPage !== this.targetPage) {
        this.updateStyleTransform(this.targetPage);
        this.addStyleTransition(allClips);
      }
      loadPage(targetPage);
    };
  }

  // create pagination control and set tooltip
  getArrow(currPage, arrowPage, loadPage) {
    const currentPage = document.createElement('div');
    // to switch on page number use : currentPage.innerText = arrowPage;
    currentPage.setAttribute('class', 'page-current');
    if (currPage !== arrowPage) {
      const wrapped = this.wrapLoadPage(arrowPage, loadPage, currPage);

      currentPage.addEventListener('pointerup', wrapped);
      this.listeners.push(() => currentPage.removeEventListener('pointerup', wrapped));
    }
    const tooltip = document.createElement('span');
    tooltip.setAttribute('class', 'page-tooltip');
    tooltip.innerText = arrowPage;
    currentPage.appendChild(tooltip);
    function showTooltip() {
      tooltip.style.opacity = 1;
    }
    currentPage.addEventListener('pointerdown', showTooltip);
    this.listeners.push(() => currentPage.removeEventListener('pointerdown', showTooltip));

    return currentPage;
  }

  // has nex page with data
  hasNextPage(page) {
    return this.data.length - (page * this.countPerPage) > 0;
  }

  // logic for displaying pagination controls
  drawArrows(page, loadPage) {
    const parent = this.getArrowsParent();
    if (page > 1) {
      parent.appendChild(this.getArrow(page, 1, loadPage));
    }
    if (page > 2) {
      parent.appendChild(this.getArrow(page, page - 1, loadPage));
    }
    parent.appendChild(this.getArrow(page, page, loadPage));

    if (this.hasNextPage(page)) {
      parent.appendChild(this.getArrow(page, page + 1, loadPage));
    }

    this.container.appendChild(parent);
  }

  /* eslint class-methods-use-this: ["error", { "exceptMethods": ["container"] }] */
  get container() {
    return document.getElementsByClassName('container')[0];
  }

  // render video items
  renderVideo(data) {
    const videoWrapper = document.createElement('div');
    videoWrapper.setAttribute('class', 'video-wrapper');

    const clips = data.map(video => this.prepareVideo(video));
    clips.forEach(it => videoWrapper.appendChild(it));

    const allClips = document.getElementsByClassName('all-clips')[0];
    allClips.appendChild(videoWrapper);
  }

  cleanListeners() {
    let a = this.listeners.pop();
    while (a) {
      a();
      a = this.listeners.pop();
    }
  }

  cleanHtml = () => {
    const pageW = document.getElementsByClassName('page-wrapper')[0];
    if (pageW) {
      pageW.remove();
    }
  };

  drawHtml(page, loadPage) {
    this.drawArrows(page, loadPage);

    // recalculate & set position
    this.updateStyleTransform(page);
  }

  cleanVideos() {
    this.data = [];
    const a = Array.from(document.getElementsByClassName('video-wrapper'));
    a.forEach(i => i.remove());
    const allClips = document.getElementsByClassName('all-clips')[0];
    allClips.style.transform = '';
  }

  // get new loaded data from AppModel & render to AllClips list
  addVideo(data) {
    this.data.push(...data);
    this.renderVideo(data);
  }

  render(data, page, loadPage) {
    // clean old listeners
    this.cleanListeners();
    // clean html
    this.cleanHtml();
    if (!this.data.length) return;
    // draw new html
    this.drawHtml(page, loadPage);
  }
}
