import AppModel from '../models/AppModel';
import AppView from '../views/AppView';
import AppStart from '../views/AppStart/AppStart';

const _ = require('lodash');

function getPageNum(itemInd, countPerPage) {
  const page = Math.floor(itemInd / countPerPage) + 1;
  return page;
}

export { getPageNum };

export default class App {
  constructor() {
    this.page = 1;
    this.view = new AppView();
    this.model = new AppModel('', this.view);
  }

  // get data if page changed
  loadPage = async (pageNumber) => {
    this.page = pageNumber;
    const data = await this.model.getPage(this.page);
    this.view.render(data, this.page, this.loadPage);

    [this.slider] = document.getElementsByClassName('video-content');
    this.isDown = false;
    this.slider.addEventListener('pointerdown', this.onPointerDown);
    this.slider.addEventListener('pointerup', this.onPointerUp);
    this.slider.addEventListener('pointermove', this.onPointerMove);
    this.view.listeners.push(() => this.slider.removeEventListener('pointerdown', this.onPointerDown));
    this.view.listeners.push(() => this.slider.removeEventListener('pointerup', this.onPointerUp));
    this.view.listeners.push(() => this.slider.removeEventListener('pointermove', this.onPointerMove));
  };

  // new search logic
  showCards = async (event) => {
    const searchStr = event.target.value;

    // new search request
    if (this.model.query !== searchStr) {
      this.page = 1;
      this.view.cleanVideos();
      this.model = new AppModel(searchStr, this.view);
      this.setPageSize();
    }
    await this.loadPage(this.page);
  };

  start() {
    AppStart.render();
    window.addEventListener('resize', this.onResize);

    const inputSearch = document.getElementsByClassName('input-search')[0];
    inputSearch.addEventListener('input', _.debounce(this.showCards, 700));
  }

  onResize = async () => {
    const firstClipInd = this.model.getFirstClip(this.page);
    this.setPageSize();
    this.page = getPageNum(firstClipInd, this.countPerPage);
    await this.loadPage(this.page);
  };

  // set number of items on page depend on screen width
  setPageSize() {
    const { width } = window.screen;
    // items per page: 1 - 4
    this.countPerPage = Math.max(Math.floor(width / this.view.videoItemWidth), 1);
    this.countPerPage = Math.min(4, this.countPerPage);

    this.model.showCount = this.countPerPage;
    this.view.countPerPage = this.countPerPage;
  }

  onPointerDown = (e) => {
    this.isDown = true;
    this.startX = e.pageX;
    this.allClips.classList.add('video-content_active');
  }

  onPointerMove = (e) => {
    if (!this.isDown) return;

    // check if it was moving
    const moved = 8;
    this.endX = e.pageX;
    const actualMove = Math.abs(this.startX - this.endX);
    if (actualMove < moved) return;

    if (this.startX - this.endX > 0) { // scroll to right
      if (!this.view.hasNextPage(this.page)) return;
      this.page += 1;
    } else if (this.page > 1) { // scroll to left
      this.page -= 1;
    }

    this.loadPage(this.page);

    const [allClips] = document.getElementsByClassName('all-clips');
    this.view.addStyleTransition(allClips);
  }

  onPointerUp = () => {
    this.isDown = false;
    this.allClips.classList.remove('video-content_active');
  }
}
