export default class AppModel {
  constructor(query, view) {
    this.view = view;
    this.clips = [];
    this.startIndex = 0;
    this.query = query;
    this.loadUrl = 'https://www.googleapis.com/youtube/v3/search?';
    this.viewUrl = 'https://www.googleapis.com/youtube/v3/videos?';
    // const key = 'AIzaSyAUqs-g3B_8n0hJa2C7MwjnsmKlDRCkoBI';
    const key = 'AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y';
    this.loadParams = {
      key,
      type: 'video',
      part: 'snippet',
      maxResults: 15,
      q: this.query,
      pageToken: '',
    };
    this.viewParams = {
      key,
      part: 'snippet,statistics',
      id: '',
    };
  }

  // return url as a string
  prepareUrl(url, params) {
    const esc = encodeURIComponent;
    const urlParams = Object.keys(params)
      .map(k => `${esc(k)}=${esc(params[k])}`)
      .join('&');
    this.url = url + urlParams;
    return this.url;
  }

  loadData = async () => {
    // no more data to load
    if (this.page !== 1 && !this.loadParams.pageToken) return;

    const loadUrl = this.prepareUrl(this.loadUrl, this.loadParams);
    const response = await fetch(loadUrl);
    const data = await response.json();

    // no data loaded
    if (!data) return;

    this.loadParams.pageToken = data.nextPageToken;

    // get video ids and load view count from API request
    const videoId = data.items.map(it => it.id.videoId);
    this.viewParams.id = videoId.join(',');

    const viewUrl = this.prepareUrl(this.viewUrl, this.viewParams);

    const responseDetail = await fetch(viewUrl);
    const dataDetail = await responseDetail.json();

    const preparedData = data.items.map((it) => {
      const video = dataDetail.items.filter(item => it.id.videoId === item.id);
      /* eslint no-param-reassign: ["error", { "props": false }] */
      it.snippet.viewCount = video[0].statistics.viewCount;
      return it;
    });

    // push clips to storage add render to page
    this.clips.push(...preparedData);
    this.view.addVideo(preparedData);
  }

  // get data for target page
  getPage = async (page) => {
    this.page = page;
    if (this.query === '') {
      this.clips = [];
      return [];
    }
    this.startIndex = (page - 1) * this.showCount;
    this.endIndex = this.startIndex + this.showCount;
    const res = this.clips.length - this.endIndex;

    if (res <= this.showCount) { // load new data async
      await this.loadData();
    } else if (res < this.showCount * 3) { // load new data in background
      this.loadData();
    }

    const showChunk = this.clips.slice(this.startIndex, this.endIndex);
    return showChunk;
  };

  // get first clip inedex for correct resizing
  getFirstClip(page) {
    const ind = (page - 1) * this.showCount;
    return ind;
  }
}
